//Fonts for the site landing-busness.hw

@font-face {
  font-family: "GeorgiaBold";
  src: url("../fonts/GeorgiaBold/GeorgiaBold.ttf");
  src: url("../fonts/GeorgiaBold/GeorgiaBold.eot"),
    url("../fonts/GeorgiaBold/GeorgiaBold.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/GeorgiaBold/GeorgiaBold.woff2") format("woff2"),
    url("../fonts/GeorgiaBold/GeorgiaBold.woff") format("woff"),
    url("../fonts/GeorgiaBold/GeorgiaBold.svg#GeorgiaBold") format("svg");
  font-weight: normal;
  font-style: normal;
}

